
import { defineComponent } from 'vue';
import router from './router';

export default defineComponent({
  name: 'App',
  setup() {
    router.afterEach((to, from) => {
      if (to.name === 'snake') {
        document.body.style.backgroundColor = 'black';
        return;
      }

      if (from.name === 'snake') {
        document.body.style.backgroundColor = 'white';
      }
    });
  },
});
